body {
  color: #f7f7f7;
  background-color: #1c1c1c;
  font-family: space grotesk, sans-serif;
  font-size: 18px;
  overflow: hidden;
}

main {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.plusButton {
  width: 60px;
  height: 60px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  color: #000;
  z-index: 1000;
  background-color: #fff3;
  border: none;
  border-radius: 50%;
  transition: transform .3s ease-in-out, background-color .3s;
  position: fixed;
  top: 10px;
  left: 10px;
  box-shadow: 0 0 10px #fff3;
}

.plusButton:active {
  transform: translateY(4px);
  box-shadow: 0 1px 5px #ffffff1a;
}

.plusButton span {
  width: 50%;
  height: 2px;
  background-color: #000;
  position: absolute;
}

.plusButton .horizontal {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.plusButton .vertical {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(90deg);
}

.plusButton.close .horizontal {
  transform: translate(-50%, -50%)rotate(45deg);
}

.plusButton.close .vertical {
  transform: translate(-50%, -50%)rotate(135deg);
}

header {
  text-align: center;
}

header h1 {
  margin: 0;
  font-family: monofett, monospace;
  font-size: 10rem;
}

.container {
  width: 80%;
  max-width: 100%;
  background-color: #1a1a1a;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: auto;
  padding: 5vh;
  display: flex;
  overflow: auto;
  box-shadow: 0 0 10px #ffffff1a;
}

.container > * {
  flex: 30%;
}

nav ul {
  background-color: #ff6a00;
  justify-content: space-around;
  align-items: center;
  list-style-type: none;
  display: flex;
}

nav ul li {
  background-color: #0000;
  margin: 0 10px;
  padding: 10px 20px;
  transition: transform .3s;
}

form {
  text-align: center;
  width: 30%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

form input[type="text"] {
  color: #f7f7f7;
  background-color: #1a1a1a;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
}

label {
  justify-content: center;
  align-items: center;
  display: flex;
}

.rating {
  direction: rtl;
  justify-content: center;
  align-items: center;
  display: flex;
}

.rating > input {
  display: none;
}

.rating > label {
  color: #ddd;
  cursor: pointer;
  margin: 0;
  font-size: 25px;
  transition: color .2s;
  display: inline-block;
}

.rating > input:checked ~ label, .rating:not(:checked) > label:hover, .rating:not(:checked) > label:hover ~ label {
  color: #fcff3f;
}

form input[type="submit"] {
  color: #1a1a1a;
  cursor: pointer;
  background-color: #ff3f3f;
  border: none;
  border-radius: 5px;
  padding: 10px;
  transition: background-color .2s;
}

form input[type="submit"]:hover {
  color: #1a1a1a;
  background-color: #f7f7f7;
}

#song-list-aside {
  width: 50%;
  max-height: 50vh;
  overflow: none;
  background-color: #1a1a1a;
  border: #000;
  border-radius: 5px;
  flex-direction: column;
  padding-bottom: 3%;
  padding-left: 3%;
  display: flex;
  box-shadow: 0 0 10px #ffffff1a;
}

#songList {
  max-height: 100%;
  flex-grow: 1;
  margin: 0;
  padding: 0;
  list-style: none;
  overflow-y: auto;
}

#songList li {
  color: #f7f7f7;
  cursor: pointer;
  background-color: #1a1a1a;
  border-bottom: 1px solid #ddd;
  padding: 10px;
}

#songList li:hover {
  background-color: #333;
}

#songList li button {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 14px;
}

#songList li button:hover {
  color: #ff3f3f;
}

form#form {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

form input[type="text"], form input[type="submit"] {
  margin: 10px auto;
  display: block;
}

#searchInput {
  display: none;
}

#searchButton {
  color: #f7f7f7;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: #ff3f3f;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(45deg);
  }
}

.rotate {
  animation: .5s linear rotation;
}

